*{
  font-family: Arial, Helvetica, sans-serif;

}

body{
  padding: 60px;
  background-color: #121212;
  color: #fff;
}

input{
  color: #fff;
  font-size:1.2rem;
  width: 100%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius:6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  background-color: transparent;
}

.card p {
  padding: 5px;
  text-align: center;
  color: #fff;
  font-weight: 700;
}

.card img{
  max-height: 170px;
}